$primaryColor: #4527a4;

// Common CSS
html {
	scroll-behavior: smooth;
}

// Dashicon
.dashicons {
	font-family: dashicons !important;
}

// Block Directory
.wp-block-ytvgb-gallery {

	* {
		box-sizing: border-box;
	}

	.ytvgbPreloader {
		text-align: center;
	}

	.ytvgbYouTubeVideoGallery {

		width: 100%;

		.videoGallery {
			display: grid;
			width: 100%;

			.singleItem {
				width: 100%;
				overflow: hidden;

				.videosDes {
					p {
						margin: 0px;
						word-wrap: break-word;
					}
				}

				.videosInfo {
					margin-top: 20px;
					display: flex;
					align-items: center;
					column-gap: 10px;

					.viewCount,
					.likeCount,
					.commentCount,
					.publishDate {
						display: flex;
						align-items: center;
						column-gap: 5px;

						span {
							font-size: 14px;
						}
					}
				}

				.iframeEle {
					width: 100%;

					iframe {
						width: 100%;
					}
				}

				h1 {
					margin: 0px;

					a {
						display: block;
						text-decoration: none;
						font-size: 18px;
						color: #000;
						font-weight: 500;
					}
				}

			}

			&.grid-details {
				.singleItem {
					width: 100%;
					overflow: hidden;

					.videosDes {
						p {
							margin: 0px;
							word-wrap: break-word;
						}
					}

					.videosInfo {
						margin-top: 20px;
						display: flex;
						align-items: center;
						column-gap: 10px;

						.viewCount,
						.likeCount,
						.commentCount {
							display: flex;
							align-items: center;
							column-gap: 5px;

							span {
								font-size: 14px;
							}
						}
					}
				}
			}

			&.playlist {
				display: initial;

				.playlistContainer {
					display: flex;
					justify-content: flex-start;
					align-items: flex-start;
					column-gap: 20px;

					.player {
						width: 70%;

						.iframeEle {
							width: 100%;

							iframe {
								width: 100%;
							}
						}
					}

					.playlist {
						width: 30%;
						overflow-y: auto;

						.singleItem {
							display: flex;
							column-gap: 10px;
							text-align: left;
							margin-bottom: 10px;

							.img {
								position: relative;
								min-width: 80px;
								height: 45px;
								cursor: pointer;

								&::after {

									content: '';
									position: absolute;
									top: 0;
									left: 0;
									width: 100%;
									height: 100%;
									background: #00000078;
								}


								img {
									width: 100%;
									height: 100%;
									object-fit: cover;
								}

								.playIcon {
									position: absolute;
									top: 3px;
									left: 3px;
									width: 20px;
									height: 20px;
									border-radius: 50%;
									border: 1px solid #fff;
									display: flex;
									align-items: center;
									justify-content: center;
									z-index: 2;

									svg {}
								}

							}

							.info {
								width: 100%;
								overflow: hidden;

								.videosTitle {
									h1 {
										a {}
									}
								}

								.videosDes {
									max-width: 100%;

									p {
										margin: 0px;
										word-wrap: break-word;
									}
								}
							}

						}
					}
				}

			}
		}

		.ytvgbLoadMoreBtn {
			margin-top: 35px;
			text-align: center;

			button {
				border: none;
				font-size: 14px;
				padding: 9px 19px;
				background: red;
				color: #fff;
				font-weight: 500;
				cursor: pointer;
			}
		}
	}
}



/*
*
*column set
*/
@for $i from 1 through 12 {
	.columns-#{$i} {
		grid-template-columns: repeat(#{$i}, 1fr);
	}
}

@media (max-width: 768px) {
	@for $i from 1 through 12 {
		.columns-tablet-#{$i} {
			grid-template-columns: repeat(#{$i}, 1fr);
		}
	}
}

@media (max-width: 576px) {
	@for $i from 1 through 12 {
		.columns-mobile-#{$i} {
			grid-template-columns: repeat(#{$i}, 1fr);
		}
	}
}